@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@200;300;400;500;600;700;800&display=swap');


*,
*:after,
*:before {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

:active,
:hover,
:focus {
    outline: 0 !important;
    outline-offset: 0;
}

a,
a:hover {
    text-decoration: none;
}

a:hover {
    color: var(--primary-color) !important;
}

ul,
ol {
    margin: 0;
    padding: 0;
}

:root {
    --primary-color: #ff553e;
    --secondary-color: #333;
    --white-color: #fff;
    --black-color: #000;
}

html{
    scroll-behavior: smooth;
}

body {
    font-size: 100%;
    font-weight: 400;
    font-family: 'Dosis', sans-serif;
}


::-webkit-scrollbar {
    width: 0.3125rem;
}

::-webkit-scrollbar-track {
    background: var(--white-color);
}

::-webkit-scrollbar-thumb {
    background: var(--primary-color);
}

h1{
    font-size: 3rem;
    font-weight: 400;
    margin-bottom: 1rem;
    text-transform: uppercase;
    color: var(--white-color);
}

h2 {
    font-size: 1.5625rem;
    font-weight: 600;
    margin-bottom: 1rem;
    text-transform: capitalize;
    color: var(--white-color);
    line-height: 2.5rem;

}

h4 {
    color: var(--white-color);
    text-transform: capitalize;
    font-size: 1.125rem;
    font-weight: 400;
    margin-bottom: 0.9375rem;
    transition: all .3s linear;

}

h5{
    color: var(--white-color);
    text-transform: capitalize;
    font-size: 0.875rem;
    font-weight: 400;
    margin-bottom: 10px;
}

p {
    font-size: 1rem;
    color: var(--white-color);
    font-weight: 400;
    line-height: 1.75rem;
    letter-spacing: 0.0625rem;
    opacity: 0.8;
}

.main-btn {
    height: 40px;
    line-height: 40px;
    font-size: 1.125rem;
    display: inline-block;
    color: var(--white-color);
    font-weight: 700;
    text-transform: uppercase;
    padding: 0 30px;
    position: relative;
}

.main-btn::after,
.main-btn::before {
    position: absolute;
    top: 50%;
    background-color: rgba(255, 255, 255, .3);
    left: 0;
    width: 100%;
    height: 1px;
    content: "";
    transition: all .5s ease-out 0s;
    -webkit-transition: all .5s ease-out 0s;
    transform: translateY(-20px);
    -webkit-transform: translateY(-20px);

}

.main-btn::after {
    transform: translateY(20px);
    -webkit-transform: translateY(20px);

}

.main-btn:hover::before {
    transform: rotate(20deg);
    -webkit-transform: rotate(20deg);
    background-color: var(--primary-color);

}

.main-btn:hover::after {
    transform: rotate(-20deg);
    -webkit-transform: rotate(-20deg);
    background-color: var(--primary-color);

}
.main-btn:hover{
    color: var(--primary-color) !important;
}

.wrapper {
    padding-top: 4rem;
    padding-bottom: 4rem;
    background-color: var(--secondary-color);
}
.text-content {
    width: 60%;
    margin: auto;
}



/*HEADER*/

.top-header{
    background-color: var(--secondary-color);
}
.top-header span{
    font-size: 0.875rem;
    color: var(--white-color);
    letter-spacing: 0.1062rem;
}
.navigation-wrap{
    top: 53px;
    position: fixed;
    width: 100%;
    left: 0;
    z-index: 1000;
    -webkit-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
}

.navigation-wrap .nav-item{
    padding: 0 0.5rem;
    transition: all 200ms linear;
}

.navbar-toggler:focus{
    outline: unset;
    border: none;
    box-shadow: none;
}

.navigation-wrap .nav-link{
    padding-top: 0.9375rem;
    font-size: 0.975rem;
    font-weight: 400;
    text-transform: uppercase;
    color: var(--white-color);
    letter-spacing: 0.0625rem;
    font-family: fangsong;

}

.navigation-wrap .nav-link:hover,
.navigation-wrap .nav-link.active{
    color: var(--primary-color);
}


.navigation-wrap.scroll-on{
    position: fixed;
    background-color: var(--black-color);
    top: 0;
    left: 0;
    width: 100%;
    box-shadow: 0 0.125rem 1.75rem 1 rgb(0, 0, 0, 0.09);
    transition: all .15s ease-in-out 0s;
}
.navigation-wrap.scroll-on .nav-link:hover,
.navigation-wrap.scroll-on.nav-link.active{
    color: var(--primary-color);
}


/*BANNER - 1*/


#home .carousel-caption{
    top: 12rem;
}

#home .carousel-caption p{
    max-width: 36.25rem;
    margin: 1.0625rem auto;
}



/* NEW ARRIVLS*/

.carimg{
    width: 94%;
}
.carimg1{
    width: 95%;
}


.new-arrivals .banner-info{
    bottom: 240px;
    margin: auto;
    width: 100%;
    position: relative;
    z-index: 9;
    padding: 0 15px;
}

.new-arrivals .card-banner:nth-child(2) h2,
.new-arrivals .card-banner:nth-child(2) p,
.new-arrivals .card-banner:nth-child(2) a{
    color: var(--black-color);
}



.new-arrivals .card-banner:nth-child(2) a::before,
.new-arrivals .card-banner:nth-child(2) a::after{
    background-color: var(--black-color);
}

.new-arrivals .card-banner .card-thumb,
.product .item-product{
    position: relative;
    display: block;
    overflow: hidden;
}

.new-arrivals .card-banner .card-thumb img,
.product .item-product img{
    transition: all .5s ease-out 0s;
}

.new-arrivals .card-banner .card-thumb img:hover,
.product .item-product img:hover{
    transform: scale(1.2);
    opacity: 0.6;
}


.ratings {
    font-size: 1.2rem;
    color: #fdcc0d;
  }
  
  #no_of_reviews {
    font-size: 0.85rem;
    color: yellow;
    margin-left: 0.5rem;
  }
  .rating-outer {
    display: inline-block;
    position: relative;
    font-family: FontAwesome;
    color: #fdcc0d;
  }
  
  .rating-outer::before {
    content: '\f006 \f006 \f006 \f006 \f006';
  }
  
  .rating-inner {
    position: absolute;
    top: 0;
    left: 0;
    white-space: nowrap;
    overflow: hidden;
    width: 0;
  }
  
  .rating-inner::before {
    content: '\f005 \f005 \f005 \f005 \f005';
    color: #f8ce0b;
  }


.pagi{
    margin-top: -280px;
    margin-bottom: 60px;
    font-family: 'fontawesome';
}
.page-item.active .page-link {
    background-color: #fa9c23;
    border-color: #fa9c23;
  }
  
  .page-link {
    color: #fa9c23;
  }
  
  .page-link:hover {
    color: #fa9c23;
  }






/* PRODUCTS */

.product .wrapper{
    background-color: var(--black-color);
}
.product .nav-tabs{
    border: 0;
    justify-content: center;
}
.product .nav-tabs .nav-link{
    display: block;
    padding: 0 30x;
    border: 1px solid var(--white-color);
    border-radius: 20px;
    background-color: var(--black-color);
    color: var(--white-color);
    height: 36px;
    margin: 10px;
    line-height: 18px;
    text-transform: uppercase;

}

.product .product-info .cat-parent,
.product .product-info .wishlist i{
    font-size: 0.875rem;
    color: var(--white-color);
    opacity: 0.6;
    text-transform: uppercase;
}

.product .product-title a{
    margin: 0 0 7px;
    display: block;
    color: var(--white-color);
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    text-transform: uppercase;
    font-size: 0.9rem;
}

.product .rating i{
    color: #f4f632;
}

.basket a i{
    color: var(--white-color);
}

/* DEALS OF DAY */

.deal-of-the-day{
    background-image: url(/public/img/card/img-4.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}





  




