    h1{
        font-size: 3.25rem;
    }

    h2{
        font-size: 1.125rem;
        line-height: 1.5625rem;
    }
    .wrapper {
        padding-top: 1.25rem;
        padding-bottom: 1.25rem;
    }
    .pagi{
        margin-top: -230px;
    }
}

