    .navigation-wrap{
        top: 0;
    }

    h4{
        font-size: 0.9375rem;
        margin-bottom: 0.625rem;
    }
    p {
        font-size: 0.875rem;
        line-height: 1.3125rem;
    }

    .text-content{
        width: 100%;
    }
    .new-arrivals .banner-info{
        bottom: 260px;
    }
    .pagi{
        margin-top: -270px;
    }

    /* navbar brand*/

    .navigation-wrap .navbar-brand img{
        height: 2.8125rem;
    }
    .navbar-toggler i{
        color: var(--white-color);
        line-height: 2;
    }
